<template>
  <el-row class="mine" type="flex" justify="space-around">
      <el-row>
        <el-row type="flex">
          <label class="btn el-button--default" for="uploads">选择照片</label>
          <input
              type="file"
              id="uploads"
              style="position:absolute; clip:rect(0 0 0 0);"
              accept="image/png, image/jpeg, image/gif, image/jpg"
              @change="uploadImg($event, 1)">
          <span style="font-size: 12px;color: #999;">仅支持JPG、JPEG、PNG格式（2M以下）</span>
        </el-row>
        <el-row class="cropper-center">
          <vueCropper
              class="cropper-panel"
              ref="cropper"
              :img="option.img"
              :outputSize="option.size"
              :outputType="option.outputType"
              :info="true"
              :full="option.full"
              :canMove="option.canMove"
              :canMoveBox="option.canMoveBox"
              :fixedBox="option.fixedBox"
              :original="option.original"
              :autoCrop="option.autoCrop"
              :fixed="option.fixed"
              :fixedNumber="option.fixedNumber"
              :autoCropWidth="option.autoCropWidth"
              :autoCropHeight="option.autoCropHeight"
              :centerBox="option.centerBox"
              :high="option.high"
              :infoTrue="option.infoTrue"
              @realTime="realTime">
          </vueCropper>
          <div class="buttonGroup">
            <el-row>
              <i class="d" @click="changeScale(1)"></i>
              <i class="x" @click="changeScale(-1)"></i>
            </el-row>
            <i class="z" @click="rotateLeft"></i>
          </div>
        </el-row>
      </el-row>
      <el-row>
        <p>成品预览</p>
        <div v-if="$refs.cropper" :style="previews.div">
          <img :src="previews.url" :style="previews.img">
        </div>
        <div v-else style="width:154px;height:154px;overflow:hidden;border-radius:50%;margin-top:20px">
          <img
              src="https://dev-space.hjhx.cn/8e9d88d5-a52d-40e5-9851-9e47d6e35cee/8e9d88d5-a52d-40e5-9851-9e47d6e35cee.jpg"
              width="100%" height="100%">
        </div>
      </el-row>
    </el-row>
</template>

<script>
import { VueCropper } from '@/components/vue-cropper'

export default {
  name: 'mine',
  components: {
    VueCropper
  },
  data () {
    return {
      option: {
        img: '',
        size: 1,
        full: false,
        outputType: 'png',
        canMove: true,
        fixedBox: true,
        original: false,
        canMoveBox: true,
        autoCrop: true,
        // 只有自动截图开启 宽度高度才生效
        autoCropWidth: 154,
        autoCropHeight: 154,
        centerBox: false,
        high: true,
        fixed: true,
        fixedNumber: [1, 1]
      },
      previews: {},
      fileList:[],
    }
  },
  methods: {
    uploadImg (e) {
      console.log('选择的装片', e)
      // 上传图片
      // this.option.img
      let file = e.target.files[0]
      console.log('file', file)
      if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
        this.$message.error('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')
        return false
      }
      let reader = new FileReader()
      reader.onload = e => {
        let data
        console.log('e.target', e.target)
        console.log('e.target.result', e.target.result)
        if (typeof e.target.result === 'object') {
          // 把Array Buffer转化为blob 如果是base64不需要
          data = window.URL.createObjectURL(new Blob([e.target.result]))
        } else {
          data = e.target.result
        }
        this.option.img = data
      }
      // 转化为base64
      // reader.readAsDataURL(file)
      // 转化为blob
      reader.readAsArrayBuffer(file)
    },
    realTime (data) {
      data.div.overflow = 'hidden'
      data.div.borderRadius = '50%'
      data.div.marginTop = '20px'
      this.previews = data
    },
    rotateLeft () {
      this.$refs.cropper.rotateLeft()
    },
    changeScale (n) {
      this.$refs.cropper.changeScale(n)
    },
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    }
  }
}
</script>
<style lang="scss">
.mine {

  .btn {
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    margin: 0 12px 0 0;
    padding: 9px 15px;
    font-size: 14px;
    border-radius: 4px;
    background: #fff;
    border: 1px solid #dcdfe6;
    color: #666;
    transition: all 0.2s ease;
    text-decoration: none;
    user-select: none;
  }

  .cropper-center {
    display: flex;
    margin-top: 30px;

    .cropper-panel {
      width: 228px;
      height: 228px;
    }

    /*justify-content: space-between;*/

    .line {
      margin-left: 100px;
      width: 1px;
      height: 318px;
      background-color: #999;
    }

    .saveCropper {
      margin-top: 50px;
    }

    .right {
      margin-left: 70px;
    }

    /*po*/
    .buttonGroup {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      i {
        display: block;
        width: 28px;
        height: 28px;
      }

      .d {
        background: url("./img/d.png") center center;
      }

      .x {
        background: url("./img/x.png") center center;
      }

      .z {
        background: url("./img/z.png") center center;
      }
    }
  }


  .title {
    margin: 0;
    color: #666;
    font-size: 14px;
    /*margin-bottom: 20px;*/
  }
}
</style>
